.cart__container {
  padding-top: 25px;
}

.cart__content {
  display: flex;
  margin-top: 24px;
}

.cart__phones-list {
  display: flex;
  flex-direction: column;
  margin-right: 16px;
}

.cart__phones-item {
  box-sizing: border-box;
  display: flex;
  align-items: center;
  padding: 31px 0 31px 27px;
  width: 752px;
  height: 128px;
  margin-bottom: 16px;
  border: 1px solid #e2e6e9;
}

.destroy {
  display: block;
  margin: 0 34px 0 0;
  padding: 0;
  border: 0;
  width: 20px;
  font-size: 30px;
  font-family: arial;
  color: #cc9a9a;
  background-color: #fff;
  outline: none;
  transition: color 0.2s ease-out;
  cursor: pointer;
}

.destroy:after {
  content: "×";
}

.destroy:hover {
  color: #af5b5e;
}

.cart__phones-image {
  width: 66px;
  height: 66px;
  margin-right: 31px;
  object-fit: contain;
}

.cart__phones-name {
  margin: 0;
  width: 295px;
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #313237;
}

.cart__button-quantity {
  box-sizing: border-box;
  width: 32px;
  height: 32px;
  font-weight: 500;
  font-size: 18px;
  background-color: #fff;
  border: 1px solid #b4bdc4;
  outline: none;
  cursor: pointer;
}

.cart__phones-quantity {
  width: 32px;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  text-align: center;
}

.cart__button-quantity:disabled {
  color: #e2e6e9;
  border: 1px solid #e2e6e9;
}

.button-increase {
  margin-right: 43px;
}

.cart__phones-price {
  font-weight: bold;
  font-size: 22px;
  line-height: 140%;
  color: #313237;
}

.cart__total-price {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  align-items: center;
  position: sticky;
  top: 62px;
  padding: 24px;
  width: 368px;
  height: 206px;
  border: 1px solid #e2e6e9;
}

.price__amount {
  margin: 0;
  font-weight: bold;
  font-size: 32px;
  line-height: 41px;
  letter-spacing: -0.01em;
  color: #313237;
}

.price__title {
  margin: 0;
  font-weight: 500;
  font-size: 14px;
  line-height: 21px;
  color: #89939a;
}

.price__line {
  width: 100%;
  margin: 24px 0;
}

.price__button-checkout {
  width: 100%;
  height: 48px;
  font-weight: 600;
  font-size: 14px;
  line-height: 21px;
  color: #fff;
  background-color: #313237;
  border: 1px solid #313237;
  outline: none;
  cursor: pointer;
  transition: 0.3s;
}

.price__button-checkout:hover {
  color: #313237;
  background-color: #fff;
}

.cart__empty {
  color: #89939a;
}
